export default {
  name: '',
  props: ['error'],
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
    reload () {
      window.location.reload()
    },
    goBack () {
      window.history.length > 1 ? this.$router.back() : this.$router.push('/')
    },
    goIndex () {
      this.$router.push('/')
    }
  },
  mounted () {
  }
}



